import { useState } from 'react';
import { Card, CardImg, Modal, ModalHeader, ModalBody, CardTitle,  } from 'reactstrap';
import { InfoCircle } from 'react-bootstrap-icons';
import gsap from 'gsap';

const Education = (props) => {

    const [educationState, setEducationState] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);

    const educations = props.educations;

    const onEducationSelect = (education) => {
        setEducationState(education);
    } 

    const toggleModal = () => { 
            setIsModalOpen(!isModalOpen);
    }

    const handleFocus = (e) => {
        
        setTimeout(()=>{
            const tl = gsap.timeline();
            tl.to(".circle", {duration: 1, rotation:360});
        }, 1000)
    }

    const wrapperFunction = (education) => {
        onEducationSelect(education);
        toggleModal();
    }

    const renderEducation = (education) =>{
        if(education != null){ 
             return(
                 <Modal size="lg" isOpen={isModalOpen} toggle={toggleModal}>
                     <ModalHeader toggle={toggleModal} cssModule={{'modal-title': 'w-100 text-center'}}>
                         {education.title}    
                     </ModalHeader>
                     <ModalBody>
                         <div className="cointainer">
                             <div className="row justify-content-center col-xs-12">
                                 <img className="img-fluid" src={education.image} alt={education.title} width="400" height="350"/>
                             </div>
                             <div className="row justify-content-center mt-5">
                                 <a href={education.link}> <InfoCircle onFocus={handleFocus()} className="circle" size={54}/></a>
                             </div>
                             <div className="row m-5">
                                 {education.description}
                             </div>
                         </div>
                     </ModalBody>
                 </Modal>
             );
        }else{
            return(
                <div></div>
            );
        }
    }

    return(
        <div className="container mt-10">
                <div className="row">
                    <div className="col-12">
                        <h3 id="education" className="text-center">Education</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    {educations.map( (education) => {
                        return(
                            <div className="col-sm-4 col-xs-12 image-container mb-2">
                                <Card onClick={() => wrapperFunction(education)} >
                                    <CardImg width="100%" object src={education.image} alt={education.title} />
                                    <CardTitle className="text-center">{education.title}</CardTitle>
                                </Card>
                             </div>
                        );
                 })}
                </div>
                <div className="row">
                  <div  className="col-12 col-md-5 m-1">
                    {renderEducation(educationState)}
                  </div>
                </div>
        </div>
    );
        
    
}

export default Education;